import { atom } from "recoil";
import moment from "moment";

const baseDate = moment("2024-12-27");

export const endDateAtom = atom({
  key: "endDate",
  default: baseDate.add(1, "days").format("YYYY-MM-DD"),
});

export const endDateRealAtom = atom({
  key: "endDate",
  default: baseDate.add(0, "days").format("YYYY-MM-DD"),
});

export const oneWeekDateAtom = atom({
  key: "startDate",
  default: baseDate.subtract(7, "days").format("YYYY-MM-DD"),
});

export const oneMonthLastAtom = atom({
  key: "oneMonthLast",
  default: baseDate.subtract(1, "month").format("YYYY-MM-DD"),
});

export const halfYearLastAtom = atom({
  key: "halfYearLast",
  default: baseDate.subtract(6, "month").format("YYYY-MM-DD"),
});

// export const nextDateAtom = atom({
//   key: "nextDate",
//   default: moment().add(1, "days").format("YYYY-MM-DD"),
// });

export const nextDateAtom = atom({
  key: "nextDate",
  default: baseDate.format("YYYY-MM-DD"),
});
